<template>
    <header id="Header"
            :style="{opacity:$store.state.menuOpacity}"
            @keyup.enter="search"
            v-show="$store.state.searchVis">
        <ul class="menu text-center">
            <li @click="menuClick('home')"
                class="item">Home</li>
            <li @click="menuClick('blog')"
                class="item">Blog</li>
            <li>
                <el-autocomplete
                        class="search"
                        placeholder="search for ..."
                        v-model="key"
                        icon="search"
                        @select="handleSelect"
                        :trigger-on-focus="false"
                        :fetch-suggestions="querySearch"
                        :on-icon-click="search">
                </el-autocomplete>
            </li>
            <i class="el-icon-search mobile-search"
               @click="searchInMobile"></i>
        </ul>
    </header>
</template>
<script>
export default {
  data () {
    return {
      key: ''
    }
  },
  methods: {
    search () {
      const that = this
      if (that.key.replace(/\s/g) === '') return false
      that.$store.commit('search', that.formatSearch(this.key))
      that.key = ''
      if (!location.hash.includes('blog')) {
        that.$router.push('/blog')
      }
    },
    // 跳转，回到顶部
    menuClick (name) {
      const that = this

      document.documentElement.scrollTop = document.body.scrollTop = 0

      that.$router.push(name)

      if (location.hash.includes('blog')) return false

      that.$store.commit('initialSearch')
    },
    // 选择博客
    handleSelect (item) {
      const that = this
      that.$router.push('/blog/blog_' + item.index)
      that.key = ''
    },
    // 前往搜索页
    searchInMobile () {
      const that = this
      that.$router.push('/search')
      that.$store.state.searchVis = false
    },
    // 模糊搜索，搜索框提示内容
    querySearch (queryString, cb) {
      const that = this
      const key = that.formatSearch(queryString)
      that.relatedBlog = []
      that.$store.state.initialBlogData.forEach((e, i) => {
        if (that.formatSearch(e.brief).includes(key) || that.formatSearch(e.title).includes(key)) {
          that.relatedBlog.push({value: e.title, index: e.url})
        }
      })
      cb(that.relatedBlog)
    },
    // 去空格转为小写搜索
    formatSearch (key) {
      try {
        return key.toString().toLowerCase().replace(/\s/g, '')
      } catch (error) {
        return ''
      }
    }
  },
  mounted () {
    if (this.$route.path === '/search') {
      this.$store.state.searchVis = false
    }
  }
}
</script>
<style lang="scss">

    @import './../assets/css/conifg/basic.config';

    #Header {

        $height: 50px;

        & {
            width: 100%;
            height: $height;
            line-height: $height - 4px;
            background: $primary-color;
            position: fixed;
            top: 0;
            z-index: 888;
            color: #fff;
            @include transition;
        }
        &:hover {
            opacity: 1 !important;
        }

        ul.menu {

            & {
                list-style: none;
                overflow: hidden;
                min-width: $minWidth;
                max-width: 1200px;
                margin: 0 auto;

            }

            li {

                &{
                    float: left;
                    min-width: 60px;
                }

                &:last-of-type {
                    float: right;
                }
            }

            li.item {

                & {
                    cursor: pointer;
                }
                @media screen and (min-width: $mobileWidth) {
                    &:hover {
                        background: $primary-color + #0f1010;
                        @include transition;
                    }
                }
            }
        }

        .search {

            & {
                float: right;
                width: 200px;
                margin-right: 20px;
            }

            input {
                height: 30px;
            }
            @media screen and (max-width: $mobileWidth) {
                & {
                    display: none;
                }
            }
        }

        .mobile-search {

            & {
                display: none;
            }

            @media screen and (max-width: $mobileWidth) {
                & {
                    display: inline-block;
                    position: absolute;
                    top: 16px;
                    right: 24px;
                    font-size: 18px;
                }
            }
        }
    }
</style>



